@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap);
#nav{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    background-color: #82eac6;
    margin-bottom: 1.5rem;
    padding: 0.5rem 1.5rem;        
}

#nav a{ margin-right: 1.5rem;  text-decoration: none; color: #333; }
#nav h2{ margin-right: 30rem; }
.header {    
    display: -webkit-flex;    
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 2rem;
    margin-bottom: 2rem;
}
  
.header__title {
    letter-spacing: 2px;
}

.header__title h1{
    position: relative;
}

.header__title h1 span{
    position: absolute;
    bottom: -0.8rem;
    left: 0.1rem;
    font-size: 42%;
}

.header__balance {
    font-size: 2rem;
    font-weight: 400;
    padding: 0.7rem 2rem;
    background-color: var(--grey);
    color: var(--light);
    border-radius: 15px;
    -webkit-align-self: flex-start;
            align-self: flex-start;
}

@media (max-width: 576px){
    .header__title h1{ font-size: 1.8rem; }
    .header__balance{ font-size: 1.5rem; }
}

.input form {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 60%;
    margin: 0 auto;
}
  
.input input {
    font-size: 18px;
    padding: 10px 15px;
    border: none;
    outline: none;
}
input:-webkit-autofill {
    background-color: transparent !important;
    -webkit-box-shadow: 0 0 0 50px white inset;
}
  
.input input[type='text'] {
    border-radius: 8px 0px 0px 8px;
    -webkit-flex: 1 1 100%;
            flex: 1 1 100%;
}
  
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
  
.input input[type='date'] {
    border-radius: 0px;
    min-width: 200px;
    margin-right: 20px;
}
  
.input input[type='submit'] {
    border-radius: 0px 8px 8px 0px;
    cursor: pointer;
    margin-left: 20px;
    color: var(--dark);
    font-weight: 600;
    text-transform: uppercase;
    transition: 0.4s;
    background-color: var(--primary);
}
  
.input input[type='submit']:hover {
    color: #fff;
}
  
.input label {
    font-size: 18px;
    color: gray;
}

.input div{ min-width: -webkit-max-content; min-width: max-content; }
@media (max-width: 991px){
    .input form{
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        width: 80%;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
    }
    .input div{ margin-left: 0.7rem; }
    .input input[type='submit'] { margin-left: 0.8rem; }
}
@media (max-width: 576px){
    .input input , .input label , .input input[type='submit'] { font-size: 16px; }
    .input input {
        padding: 8px 12px;
    }

    .input select{
        margin-left: 0.7rem;
        margin-bottom: 0.5rem;
    }

}
.item {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    padding: 0.8rem 5rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-bottom: 2px solid var(--grey);
    position: relative;
  }
  
  .item .income {
    color: green;
  }
  
  .item .expense {
    color: red;
  }
  
  .item__info {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .item__info p {
    padding-right: 3rem;
    font-size: 1.1rem;
  }
  
  .item__title {
    font-size: 1.2rem;
  }
  
  .item__delete {
    position: absolute;
    right: 0;
    font-size: 0.9rem;
    color: var(--alert);
    transition: 0.4s;
    text-transform: uppercase;
    display: none;
    cursor: pointer;
    font-family: inherit;    
  }

  @media (max-width: 786px){
    .item{
      padding: 0.6rem 1rem;
    }
  }  

@media (max-width: 576px){
  .item__title{ font-size: 1.1rem; }
  .item__info p{ font-size: 1.0rem; }
  .item__delete{ font-size: 0.8rem; }
}
.item-list {
    margin: 0 auto;
    margin-top: 3rem;
    width: 70%;
}

@media (max-width: 786px){
    .item-list{ width: 88%; }
}
:root {
  --light: #fffffc;
  --dark: #202c59;
  --grey: #4c5b5c;
  --primary: #ff7f11;
